 .suggest-container {
        border: 1px solid #ddd;
        padding: 5px;
        border-radius: 30px;

      }

      .suggest-items {
        display: inline-block;
        padding: 2px;
        border: 1px solid blue;
        border-radius: 5px;
        margin-right: 5px;
        cursor: pointer
      }

      .suggest-input {
        outline: none;
        border: none;
        font-size: 14px;
      }


