.modal-body {
    position: relative;
    flex: 1 1 auto;
    padding: 1.5rem 1.5rem;
}

.modal-body .form-group {
    margin-bottom: 1.8rem;
}

.modal-footer{
    padding: .5rem 1rem;
    background: #f4fdfe;
    border-top: 1px solid #e2ebec; 
}

.btn-group-popup {
    margin: auto;
}

.modal-dialog .modal-content {
    border: 0;
    border-radius: 1rem;
    padding: 0px 0 0 !important;
    overflow: hidden;

}

.form-control {
    border-radius: 20px;
}

.modal-dialog.full-width-modal-section.organization-modal{
  max-width: 760px;
}

.MuiTableCell-body {
    color: rgba(0, 0, 0, 0.87);
    width: 120px;
