}

a.btn-view, button.btn-view, div.btn-view {
    background: #ffc108;
    padding: 5px;
    width: 30px;
    height: 30px;
    border: 1px solid #ffc108;
    text-align: center;
    border-radius: 15px;
    margin: 0 2px;
    color: #ffffff;
    display: inline-block;
}

