.categoryAdd-1 {
    background: #fff;
    box-shadow: none;
    border-radius: 8px;
    text-transform: capitalize;
    padding: 8px 20px;
    color: #276f7b;
    position: absolute;
    right: 170px;
    border: 1px solid #276f7b;
    top: 2px;
}

.categoryAdd-1:hover{color: #276f7b; box-shadow: none;} 

.MuiTableCell-head {
    background: #f2f7f8 !important;
    border: none;
}

.addDrs{background: #f6f8f9;
    padding: 20px;}
    

.select-user-info {
    background: #c8e7ef;
    text-align: center;
    padding: 0px 0px;
    border-radius: 10px;
    overflow: hidden;
    align-items: center;
    border: 1px solid #c8e7ef;
    min-height: 40px;
    line-height: 38px;
    color: #266e7b;
    font-weight: 500;
}

.remove-selected-user-info {
    float: right;
    background: #83d2e0;
    border: none;
    border-radius: 8px;
    padding: 0px 12px;
    color: #fff;
}